.slider {
  display: flex;
  flex-direction: row;
  justify-content: center;
  aling-items: center;
  gap: 2.5rem;
  padding-top: 5rem;
  flex-wrap: wrap;
}

@media (max-width: 1000px) {
  .hideslider {
    display: none !important;
  }
}

@media (max-width: 540px) {
  .hideslider {
    display: none;
  }
}
