.card__container {
  border: 0px solid black;
  width: 260px;
  background-color: #cee2f1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  border-radius: 5px;
}

.card__container > p {
  font-size: 15px;
  font-weight: 500;
}

.details__container {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  overflow: hidden;
}

.card__location {
  color: rgb(78, 160, 255);
}

.fun__box {
  box-shadow: -5px 5px 12px -5px #000;
  background-image: url("../images/img.png");
  background-repeat: no-repeat;
  background-size: contain;
}
