.card__franchise {
  height: 14vh;
  /* border: 1px solid black; */
  max-width: 16vw;
  display: flex;
  justify-content: space-around;
  flex-direction: column !important;
  align-items: center;
  box-shadow: -1px 4px 9px -4px black;
}

.franchise__text {
  background-color: rgb(38, 125, 207);
}

.franchise__text > div {
  text-align: center;
  height: max-content;
  padding: 15px;
  color: white;
  font-weight: 800;
}

.franchise__image {
  width: 16vw;
  margin-bottom: 50px;
}

.card__franchise > img {
  justify-content: space-around;
  max-width: 12vw;
  height: 80px;
}

.cx-border {
  border: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 120px !important;
  box-shadow: -1px 4px 9px -4px black;
}

*::-webkit-scrollbar {
  display: none;
}

.imag__in > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
