.newcard__container {
  width: 13vw;
  border: 1px solid rgb(52, 123, 255);
  height: 15vh;
  box-shadow: -4px 3px 0px 0px rgb(52, 123, 255);

  margin: 20px;
  border-radius: 2px;
}

.name {
  height: 55px;
  overflow: hidden;
}

.ncard__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.shadow__card {
  border: 1px solid rgb(52, 123, 255);
  box-shadow: -5px 5px 0px 0px rgb(52, 123, 255);
}

@media (max-width: 540px) {
  .shadow__card {
    box-shadow: 0;
  }
}

@media (max-width: 1000px) {
  .shadow__card {
    box-shadow: none;
  }
}
