.set__input {
  background: transparent;
  border: none;
  height: 80%;
  width: 90%;
}

.container__input {
  border: 1px solid #fff;
  height: 48px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  font-size: 30px;
}

.container__input > input:focus {
  outline: 0px;
  color: white;
}

.container__input > input::placeholder {
  color: white;
}

.set__button {
  border: 0px;
  height: 48px;
  background: white;
  border-radius: 70px !important;
  position: relative;
  left: -5%;
}

@media (max-width: 540px) {
  .set__button {
    display: none !important;
  }
}

@media (max-width: 1080px) {
  .set__button {
    display: none !important;
  }
}
