.header__brd {
  border-bottom: 1px solid yellow;
  width: 99.999999999999999%;
  /* width: 100vw !important; */
}
.logo__header {
  width: 180px;
}

.sell__button {
  border: 1px solid white;
  background: transparent;
  color: white;
  border-radius: 30px;
}

.sell__button:active {
  outline: 0px;
  opacity: 0.8;
}

.sell__button:focus {
  outline: 0px;
}

.menu_buttonheader {
  display: none;
}

.nav__mobile {
  display: none;
}

@media (max-width: 540px) {
  .header__brd {
    border-bottom: 1px solid white;
    display: none;
  }

  .nav__mobile {
    display: block;
    width: 95%;
  }

  .logo__header {
    margin-left: 10px;
    width: 160px;
  }

  .header__web {
    display: none;
    width: 0px;
    height: 0px;
  }

  .sell__button {
    display: none;
    width: 0;
  }

  .menu_buttonheader {
    display: block;
  }
}
