.info__card {
  display: flex;
  height: 200px;
  /* border: 1px solid black; */
  background-color: rgb(38, 125, 207);
  padding: 10px;
  margin-bottom: 20px;
  justify-content: center;
  align-items: center;
}

.container__subscribe {
  font-weight: 800;
  color: white;
}

.container__subsicribebutton > button {
  border: 1px solid white;
  background-color: white;
  color: rgb(38, 125, 207);
  border-radius: 50px;
}

.container__subsicribebutton > button:focus {
  outline: 0px;
}

.container__subsicribebutton > button:active {
  opacity: 0.8;
}
